<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="content"  v-if="showdetail">
          <div class="code_top">
            <img src="../assets/images/wait.png" v-if="condata.Status=='-1'">
            <img src="../assets/images/succ.png" v-if="condata.Status=='1'">
            <img src="../assets/images/fail.png" v-if="condata.Status=='0'">
            <h1><span>{{condata.Value}} {{condata.Symbol}}</span> </h1>
          </div>
          <!-- //收付款信息 -->
          <div class="order">
            <p class="tname">{{$t('order.fromaddres')}}</p>
            <span>{{condata.Sender}}</span>
            <p class="tname">{{$t('order.toaddres')}}</p>
            <span>{{condata.Receiver}}</span>
             <p class="tname">{{$t('data.Consumptionfuel')}}</p>
            <span>{{condata.GasUsed}}</span>
            <p class="tname">{{$t('order.kgf')}}</p>
            <span>{{condata.kgf}} eth</span>
            <p class="tname">{{$t('order.mome')}}</p>
            <span>{{condata.Remark}}</span>
          </div>
          <hr style="height: 1px;border: none;background: #f1f1f1;margin-top: .15rem">
          <!-- //交易时间等信息 -->
          <div class="orderbig">
            <div class="order fl">
              <p class="tname">{{$t('order.onum')}}</p>
              <span>{{condata.TxNo | tradenum}}</span>
              <p class="tname">{{$t('order.block')}}</p>
              <span>{{condata.BlockNum}}</span>
              <p class="tname">{{$t('order.time')}}</p>
              <span>{{condata.CreateTime}}</span>
            </div>
            <div class="order_img fr">
                <div class="code_center">
                  <div id="qrcode"></div>
                  <button type="button"
                    v-clipboard:copy="condata.erq"
                    v-clipboard:success="onCopy"
                    v-clipboard:error="onError">{{$t('code.copy')}}</button>
                </div>
            </div>
          </div>
        </div>
        <div class="zw_ord" v-else>
          <img src="../assets/images/ord.png">
          <p>订单正在处理中</p>
        </div>
        
    </div>
  
</template>

<script>
  import { getOrder,price,GetGasPrice } from '../assets/js/Keytoken.js'
  import QRCode from 'qrcodejs2'
  import headerBar from './header.vue'
export default {
  beforeCreate () {
    var url = this.$route.path
    setTimeout(function(){
      document.querySelector('body').setAttribute('style', 'background:#ffff')
    },10)
  },
  beforeDestroy () {
    //document.querySelector('body').setAttribute('style', 'background:#f4f4f4')
  },
  components:{
      "headerBar":headerBar,
  },
  data () {
    return {
      message:'55556',
      showdetail:false,
      headerNavData:{
         left:true,
         title:this.$t('order.jlxq'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      condata:''
    }
  },
  created(){
    var that = this
    this.openloading(this.$t('toast.loding'))
    if(this.$route.query.type==1){
      if(this.$route.query.data!=null&&this.$route.query.data!=""&&this.$route.query.data!=undefined){
        that.closeloading()
        this.condata = JSON.parse(this.$route.query.data)
        this.condata.kgf = GetGasPrice(1,this.condata.GasPrice*this.condata.GasUsed)
        this.condata.erq = 'https://etherscan.io/tx/' + this.condata.TxNo
        that.showdetail=true
        setTimeout(function(){
          this.qrcode()  
        }.bind(this),100)
      }
    }else{
      this.axios({
        url:this.Url + 'EthTrade.ashx',
        method:'post',
        params: {req:5,id:this.$route.query.id}
      }).then(res => {
        that.closeloading()
        this.condata = res.data.data
        this.condata.kgf = GetGasPrice(1,this.condata.GasPrice*this.condata.GasUsed)
        this.condata.erq = 'https://etherscan.io/tx/' + this.condata.TxNo
        that.showdetail=true
        setTimeout(function(){
          this.qrcode()  
        }.bind(this),100)
      })
    }
    
  },
  methods:{
    //生成二维码
    qrcode (num) {  
      var that = this
        let qrcode = new QRCode('qrcode', {  
            width: 120,  
            height: 120, 
            text: that.condata.erq
        })   
    }, 
    onCopy: function (e) {
        this.$dialog.toast({
            mes: this.$t('data.copycontent'),
            timeout: 2000
        });
      },
      onError: function (e) {
        this.$dialog.toast({
            mes: this.$t('data.copycontentfail'),
            timeout: 2000
        });
      }
  },
  mounted () {  
      
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.zw_ord{
  width: 200px;
  position: fixed;
  height: 280px;
  top: 50%;
  left: 50%;
  margin-top: -150px;
  margin-left: -100px;
}
.zw_ord img{
  width: 100%;
}
.zw_ord p{
  text-align: center;
  font-size: .3rem;
  color: #ddd;
}
.code_top{
  padding: .35rem 0 .35rem;
  text-align: center;
}
.code_top img{
  width: .85rem;
  height: .85rem;
  border-radius: 50%;
  margin-bottom: .35rem
}
.code_top h1{
  font-size: .28rem;
  color: #333;
  font-weight: normal;
}
.code_top h1 span{
  font-size: .42rem;
  color: #278eed;
}
.order{
  padding: .1rem .4rem .4rem;
}
.tname{
  color: #888;
  font-size: .28rem;
  margin-top: .15rem;
  margin-bottom: .08rem
}
.order span{
  font-size: .28rem;
  color: #666;

}
.fl{
  float: left;
}
.fr{
  float: right;
}
.order_img{
  padding: .55rem .55rem 0 0;
}
.code_center button{
  border: none;
  width: 100%;
  height: .5rem;
  line-height: .5rem;
  text-align: center;
  background: #eee;
  color: #333;
  margin-top:.3rem;
  border-radius: 4px;
}
.code_center button:active{
  background: #278eed;
  color: #fff;
}
</style>



